.skillsBlock {
    height: 100vh;
    background-color: beige;
}
.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}
.title {
    background-color: azure;
}
.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
}

@media (max-width: 865px) {
    .skills {
        border: none;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .skillsBlock {
        height: auto;
    }
}